
import { AjaxHttp, AjaxSilentHttp } from '../../../js/ajaxHttp'
import { isArrayType, getImageUrl, getYMDFormateTime } from "../../../js/utility.js"
import ListCell from '../../crm/components/ListCell.vue'
import { mapState } from 'vuex'
import event from '../../../js/Event.js'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'



export default {
name: 'examinelist',
components: {
ListCell,
Flexbox,
FlexboxItem
},
computed: {
...mapState([
'examineList'
]),
},
data() {
return {
nav_list: [],
nav_type: 1,
itemlist: [],
p: 1,
filterShow: false,
filterBackShow: false, //过滤半透明背景
stateIndex: 0,
stateList: [
{ name: '全部', value: '' },
{ name: '审批通过', value: '2' },
{ name: '审批拒绝', value: '3' },
{ name: '审批中', value: '1' }
],
typeIndex: 0,
typeList: [
{ name: '全部', value: '' },
{ name: '普通审批', value: '1' },
{ name: '请假审批', value: '2' },
{ name: '普通报销', value: '3' },
{ name: '差旅报销', value: '4' },
{ name: '出差申请', value: '5' },
{ name: '借款申请', value: '6' }
],
}
},
beforeDestroy() {
event.$off("filterData"); 
},
beforeRouteLeave(to, from, next) {
var type = '';
if (this.nav_type === 1) {
type = 'me_examine'; 
} else if (this.nav_type === 2) {
type = 'examined'; 
}
this.examineList[type] = {
type: type, //平台类型与type相同 进行赋值
itemlist: this.itemlist,
p: this.p,
stateIndex: this.stateIndex,
typeIndex: this.typeIndex,
refresh: false,
scroll: this.$refs.my_scroller.getPosition().top,
loadingState: this.$refs.my_scroller.loadingState,
};

this.examineList.examine_type = this.nav_type; 
this.examineList.nav_type = this.nav_type;
this.$store.commit('updateExamineList', this.examineList);
next();
},
mounted() {
this.nav_list = [{ name: '待我审批的', type: 1 }, { name: '我已审批的', type: 2 }];

event.$on('filterData', function() {
this.filterData();
}.bind(this));

this.nav_type = this.examineList.nav_type ? this.examineList.nav_type : 1;
this.handleCache(this.nav_type);
},
methods: {
infinite(done) {
this.getList(done);
},
refresh(done) {
this.p = 1;
this.getList(done);
},
getList(done) {
var postParams = {};
postParams['p'] = this.p;
if (this.nav_type === 1) {
postParams['by'] = 'me_examine'; 
} else if (this.nav_type === 2) {
postParams['by'] = 'examined'; 
if (this.stateIndex !== 0) {
var item = this.stateList[this.stateIndex];
postParams['examine_status'] = item.value;
}
}

if (this.typeIndex !== 0) {
var item = this.typeList[this.typeIndex];
postParams['type'] = item.value;
}

AjaxSilentHttp('POST', 'm=examine&a=index', postParams, this, function(res, wself, success) {
if (success && isArrayType(res.list)) {
if (wself.p === 1) {
wself.itemlist = res.list;
} else {
wself.itemlist = wself.itemlist.concat(res.list);
}
if (typeof done === 'function') {
if (wself.p === 1 && res.list.length < 10) {
done(true);
} else if (res.list.length === 0) {
done(true);
} else {
done();
}
}
wself.p = wself.p + 1;
}
});
},
getShowTitle(item) {
if (item.user_info) {
return item.user_info.full_name + this.getTypeName(item.type);
}
return '';
},
getShowOne(item) {
if (item.type === '1') {
return '申请事由：' + item.content;
} else if (item.type === '2') {
return '请假天数：' + item.duration;
} else if (item.type === '3' || item.type === '4') {
return '报销金额(元)：' + item.budget;
} else if (item.type === '5') {
return '目的地：' + item.end_address;
} else if (item.type === '6') {
return '借款金额(元)：' + item.budget;
} else {
return '';
}
},
getShowTwo(item) {
if (item.type === '1') {
return '';
} else if (item.type === '2') {
return '开始时间：' + getYMDFormateTime(item.start_time);
} else if (item.type === '3' || item.type === '4') {
return '报销事由：' + item.content;
} else if (item.type === '4') {
return '的报销';
} else if (item.type === '5') {
return '出发时间：：' + getYMDFormateTime(item.start_time);
} else if (item.type === '6') {
return '借款事由：' + item.content;
} else {
return '';
}
},
getShowThree(item) {
if (item.type === '1') {
return '';
} else if (item.type === '2') {
return '开始时间：' + getYMDFormateTime(item.end_time);
} else if (item.type === '3' || item.type === '4') {
return '';
} else if (item.type === '5') {
return '返回时间：' + getYMDFormateTime(item.end_time);
} else if (item.type === '6') {
return '';
} else {
return '';
}
},
getShowFour(item) {
if (item.type === '1') {
return '';
} else if (item.type === '2') {
return '请假事由：' + item.description;
} else if (item.type === '3' || item.type === '4') {
return '';
} else if (item.type === '5') {
return '';
} else if (item.type === '6') {
return '';
} else {
return '';
}
},
getUserImage(item) {
if (item.user_info) {
return getImageUrl(item.user_info.thumb_path);
}
return '';
},
getCreateTime(item) {
return getYMDFormateTime(item.create_time);
},
getExamineStatus(status) {
if (status === '0') {
return '待审';
} else if (status === '1') {
return '审批中';
} else if (status === '2') {
return '通过';
} else if (status === '3') {
return '驳回';
} else {
return '';
}
},
getTypeName(type) {
if (type === '1') {
return '的审批';
} else if (type === '2') {
return '的请假';
} else if (type === '3') {
return '的报销';
} else if (type === '4') {
return '的报销';
} else if (type === '5') {
return '的出差';
} else if (type === '6') {
return '的借款';
} else {
return '';
}
},
clickNav(type) {
if (this.nav_type !== type) {
this.saveCache(this.nav_type); 
this.nav_type = type;
this.itemlist = [];
this.p = 1;
this.handleCache(this.nav_type); 
}
},
saveCache(nav_type) {
var type = '';
if (nav_type === 1) {
type = 'me_examine'; 
} else if (nav_type === 2) {
type = 'examined'; 
}
this.examineList[type] = {
type: type, //平台类型与type相同 进行赋值
itemlist: this.itemlist,
p: this.p,
stateIndex: this.stateIndex,
typeIndex: this.typeIndex,
refresh: false,
scroll: this.$refs.my_scroller.getPosition().top,
loadingState: this.$refs.my_scroller.loadingState,
};
},
handleCache(nav_type) { //操作缓存
if (nav_type === 1 && this.examineList.me_examine) {
this.stateIndex = this.examineList.me_examine.stateIndex;
this.typeIndex = this.examineList.me_examine.typeIndex;
if (this.examineList.me_examine.refresh) {
this.p = 1;
this.itemlist = [];
} else {
this.itemlist = this.examineList.me_examine.itemlist;
this.p = this.examineList.me_examine.p;
this.$nextTick(() => {
this.$refs.my_scroller.resize();
this.$refs.my_scroller.scrollTo(0, this.examineList.me_examine.scroll, false);
if (this.examineList.me_examine.loadingState === 2) {
this.$refs.my_scroller.finishInfinite(true);
}
});
}

} else if (nav_type === 2 && this.examineList.examined) {
this.stateIndex = this.examineList.examined.stateIndex;
this.typeIndex = this.examineList.examined.typeIndex;
if (this.examineList.examined.refresh) {
this.p = 1;
this.itemlist = [];
} else {
this.itemlist = this.examineList.examined.itemlist;
this.p = this.examineList.examined.p;
this.$nextTick(() => {
this.$refs.my_scroller.resize();
this.$refs.my_scroller.scrollTo(0, this.examineList.examined.scroll, false);
if (this.examineList.examined.loadingState === 2) {
this.$refs.my_scroller.finishInfinite(true);
}
});
}
} else {
this.stateIndex = 0;
this.typeIndex = 0;
this.$refs.my_scroller.finishInfinite(false);
}
},
enterItemDetail(item) {
this.$router.push({
name: 'examineview',
params: {
id: item.examine_id
}
});
},
filterData() { //暂时用于筛选
this.filterShow = !this.filterShow;
this.filterBackShow = !this.filterBackShow;
},

clickFilterBackShow() { //点击黑色背景
this.filterShow = false;
this.filterBackShow = false;
},
filter(action, index) {
if (action === 'state') {
this.stateIndex = index;
} else if (action === 'type') {
this.typeIndex = index;
}
this.p = 1;
this.itemlist = [];
this.$refs.my_scroller.finishInfinite(false);
this.clickFilterBackShow();
},
getCheckColor(check) {
if (check === '0') {
return '#FACB42';
} else if (check === '1') {
return '#FF6600';
} else if (check === '2') {
return '#5ACD61';
} else if (check === '3') {
return '#FF6600';
}
}
}
}




